<script>
import GxTrialBalanceLedgerAccount from '@components/_CoreTaxes/LedgerAccounts//GxTrialBalanceLedgerAccount'

export default {
  name: 'GxTrialBalance',
  data() {
    return {}
  },
  props: {
    trialBalance: {
      type: Object,
      default: null,
      required: true,
    },
  },
  components: {
    GxTrialBalanceLedgerAccount,
  },
  methods: {
    getNumberFormat(text) {
      if (text === '') return '-'
      if (text === '0') return '-'
      if (text === '0.00') return '-'
      if (text.includes('.')) {
        return text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\d(?=(\d{3})+\.)/g, '$&,')
      } else {
        //return (
        text = text
          .replace(/^\d+\.$/, '$&00')
          .replace(/\.\d$/, '0$&0')
          .replace(/\d+\.$/, '$&00')
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
        if (text == '0') return ''
        if (text == '0' || text == '') return text
        return text + '.00'
      }
    },
  },
}
</script>

