<script>
import GxTrialBalance from '@components/_CoreTaxes/Reports/TrialBalance/GxTrialBalance'
import GxPeriodsDropdown from '@components/_CoreTaxes/Periods/GxPeriodsDropdown'
import GxButtonActions from '@components/_CoreTaxes/Buttons/ButtonActions/GxButtonActions'
import GxDropdown from '@components/_Core/Forms/GxDropdown'
import GxButtonTaxes from '@components/_CoreTaxes/Buttons/Button/GxButtonTaxes'
import GxModal from '@components/_Core/Modals/GxModal'
import GxLoader from '@components/_Core/Utils/GxLoader'

import {
  GET_LEVELS_LEDGER_ACCOUNTS,
  GET_TRIAL_BALANCE,
  GET_TRIAL_BALANCE_PDF_OR_EXCEL,
  GET_TRIAL_BALANCE_XML,
  GET_JOURNAL_ENTRIES_XML,
} from '@src/apollo/queries'

export default {
  name: 'TrialBalance',
  data() {
    return {
      level: '2',
      levels: null,
      beginningPeriod: null,
      endingPeriod: null,
      beginningPeriodId: null,
      endingPeriodId: null,
      trialBalance: null,
      skipTrialBalance: true,
      typeFileDownload: '',

      optionsTypeTrialBalanceXML: ['Normal', 'Complementaria'],
      showDownloadTrialBalanceXML: false,
      typeTrialBalanceXML: '',
      dateTrialBalanceXML: this.getDateFormat,
      downloadingTrialBalanceXML: true,

      optionsJournalEntriesXML: [
        'Acto de Fiscalización (AF)',
        'Fiscalización Compulsa (FC)',
        'Devolución (DE)',
        'Compensación (CO)',
      ],
      showDownloadJournalEntriesXML: false,
      typeJournalEntriesXML: '',
      numberJournalEntriesXML: '',
      downloadingJournalEntriesXML: true,
    }
  },
  components: {
    GxTrialBalance,
    GxPeriodsDropdown,
    GxDropdown,
    GxButtonActions,
    GxButtonTaxes,
    GxModal,
    GxLoader,
  },
  apollo: {
    getLevelsLedgerAccounts: {
      query: GET_LEVELS_LEDGER_ACCOUNTS,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
        }
      },
      update(data) {
        this.levels =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.levels
      },
      fetchPolicy: 'no-cache',
    },
    getTrialBalancePDFOrExcel: {
      query: GET_TRIAL_BALANCE_PDF_OR_EXCEL,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          beginningPeriodId: this.beginningPeriodId,
          endingPeriodId: this.endingPeriodId,
          detailLevel: this.level,
          assumeLastAnnualPeriodIsClosed: true,
          fileType: this.typeFileDownload,
        }
      },
      update(data) {
        window.open(
          data.myAccountingFirms[0].clients[0].accounting.reports
            .trialBalanceExport.url,
          'Download'
        )
        this.typeFileDownload = ''
      },
      skip() {
        return this.typeFileDownload === ''
      },
      fetchPolicy: 'no-cache',
    },
    getTrialBalance: {
      query: GET_TRIAL_BALANCE,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          beginningPeriodId: this.beginningPeriodId,
          endingPeriodId: this.endingPeriodId,
          detailLevel: this.level,
          assumeLastAnnualPeriodIsClosed: true,
        }
      },
      update(data) {
        this.trialBalance =
          data.myAccountingFirms[0].clients[0].accounting.reports.trialBalance
      },
      skip() {
        return (
          this.skipTrialBalance ||
          !(this.beginningPeriodId !== null && this.endingPeriodId)
        )
      },
      fetchPolicy: 'no-cache',
    },
    getTrialBalanceXML: {
      query: GET_TRIAL_BALANCE_XML,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          periodId: this.endingPeriodId,
          esComplementaria:
            this.typeTrialBalanceXML === 'Normal' ? false : true,
          ultimaModificacionContable: this.dateTrialBalanceXML,
        }
      },
      update(data) {
        window.open(
          data.myAccountingFirms[0].clients[0].accounting.reports
            .additionalReportsLocalized
            .contabilidadElectronicaBalanzaDeComprobacion.url,
          'Download'
        )
        this.downloadingTrialBalanceXML = true
        this.typeTrialBalanceXML = ''
        this.showDownloadTrialBalanceXML = false
      },
      skip() {
        return this.downloadingTrialBalanceXML
      },
      fetchPolicy: 'no-cache',
    },
    getJournalEntriesXML: {
      query: GET_JOURNAL_ENTRIES_XML,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
          periodId: this.endingPeriodId,
          tipoDeSolicitud: this.getEnumRequestType,
          numeroDeOrdenOTramite: this.numberJournalEntriesXML,
        }
      },
      update(data) {
        window.open(
          data.myAccountingFirms[0].clients[0].accounting.reports
            .additionalReportsLocalized.contabilidadElectronicaPolizas.url,
          'Download'
        )
        this.downloadingJournalEntriesXML = true
        this.typeJournalEntriesXML = ''
        this.numberJournalEntriesXML = ''
        this.showDownloadJournalEntriesXML = false
      },
      skip() {
        return this.downloadingJournalEntriesXML
      },
      fetchPolicy: 'no-cache',
    },
  },
  watch: {
    endingPeriodId(newVal) {
      if (newVal === '') return
      if (newVal === null) return

      this.dateTrialBalanceXML = new Date(
        Number(this.endingPeriodId.toString().substring(0, 4)),
        Number(this.endingPeriodId.toString().substring(4)),
        0
      ).toISOString()
      // value
      // dateTrialBalanceXML
    },
    typeJournalEntriesXML() {
      this.numberJournalEntriesXML = ''
    },
  },
  computed: {
    getEnumRequestType() {
      if (this.typeJournalEntriesXML === 'Acto de Fiscalización (AF)')
        return 'actoDeFiscalizacion'
      if (this.typeJournalEntriesXML === 'Fiscalización Compulsa (FC)')
        return 'fiscalizacionCompulsa'
      if (this.typeJournalEntriesXML === 'Devolución (DE)') return 'devolucion'
      if (this.typeJournalEntriesXML === 'Compensación (CO)')
        return 'compensacion'
      return ''
    },
    getLevelsList() {
      let array = new Array()
      let index = 1 //this.levels
      while (index <= this.levels) {
        array.push(String(index))
        index++
      }
      return array
    },
    getButtonsActions() {
      let actions = [
        {
          name: 'Descargar balanza de comprobación en Excel',
          icon: 'cloud_download',
          type: 'method',
          action: 'downloadExcel',
        },
        {
          name: 'Descargar balanza de comprobación en PDF',
          icon: 'cloud_download',
          type: 'method',
          action: 'downloadPDF',
        },
        {
          name: 'Descargar balanza en XML para contabilidad electrónica',
          icon: 'cloud_download',
          type: 'method',
          action: 'trialBalanceXML',
        },
        {
          name: 'Descargar pólizas en XML para contabilidad electrónica',
          icon: 'cloud_download',
          type: 'method',
          action: 'journalEntriesXML',
        },
      ]
      return actions
    },
    getLabelTextField() {
      if (this.typeJournalEntriesXML === '') return ''
      if (
        this.typeJournalEntriesXML === 'Acto de Fiscalización (AF)' ||
        this.typeJournalEntriesXML === 'Fiscalización Compulsa (FC)'
      )
        return 'Número de orden *'
      if (
        this.typeJournalEntriesXML === 'Devolución (DE)' ||
        this.typeJournalEntriesXML === 'Compensación (CO)'
      )
        return 'Número de trámite  *'
      return ''
    },
    getMaximumCharactersTextField() {
      if (this.typeJournalEntriesXML === '') return 0
      if (
        this.typeJournalEntriesXML === 'Acto de Fiscalización (AF)' ||
        this.typeJournalEntriesXML === 'Fiscalización Compulsa (FC)'
      )
        return 13
      if (
        this.typeJournalEntriesXML === 'Devolución (DE)' ||
        this.typeJournalEntriesXML === 'Compensación (CO)'
      )
        return 14
      return 0
    },
  },
  created() {
    this.beginningPeriod = localStorage.getItem('beginningPeriod')
    this.endingPeriod = localStorage.getItem('endingPeriod')
    if (localStorage.getItem('periodDefault')) {
      this.beginningPeriodId = Number(localStorage.getItem('periodDefault'))
      this.endingPeriodId = Number(localStorage.getItem('periodDefault'))
    } else {
      this.beginningPeriodId = this.endingPeriod
      this.endingPeriodId = this.endingPeriod
    }
    setTimeout(() => {
      this.$refs.PeriodInit.changeValue(this.beginningPeriodId)
      this.$refs.PeriodEnd.changeValue(this.endingPeriodId)
    }, 50)
  },
  methods: {
    selectedPeriodInit(period) {
      //console.log(period)
      this.skipTrialBalance = true
      this.trialBalance = null
      if (
        period > this.endingPeriodId ||
        period.toString().slice(0, 4) !==
          this.endingPeriodId.toString().slice(0, 4)
      ) {
        this.$refs.PeriodEnd.changeValue(period)
        this.endingPeriodId = period
      }
      this.beginningPeriodId = period
      this.skipTrialBalance = false
    },
    selectedPeriodEnd(period) {
      // console.log(period)
      this.skipTrialBalance = true
      this.trialBalance = null

      if (
        period < this.beginningPeriodId ||
        period.toString().slice(0, 4) !==
          this.beginningPeriodId.toString().slice(0, 4)
      ) {
        this.beginningPeriodId = period
        this.$refs.PeriodInit.changeValue(period)
      }
      this.endingPeriodId = period
      this.skipTrialBalance = false
    },
    downloadPDF() {
      this.typeFileDownload = 'pdf'
    },
    downloadExcel() {
      this.typeFileDownload = 'excel'
    },
    trialBalanceXML() {
      this.showDownloadTrialBalanceXML = true
      this.typeTrialBalanceXML = ''
      //console.log('trialBalanceXML')
    },
    closeTrialBalanceXML() {
      this.showDownloadTrialBalanceXML = false
      //console.log('trialBalanceXML')
    },
    journalEntriesXML() {
      //console.log('journalEntriesXML')
      this.showDownloadJournalEntriesXML = true
    },
    closeJournalEntriesXML() {
      //console.log('journalEntriesXML')
      this.showDownloadJournalEntriesXML = false
      this.typeJournalEntriesXML = ''
    },
    validateJournalEntriesXML() {
      if (this.$refs.NumberJournalEntriesXML.value === '') {
        this.$refs.NumberJournalEntriesXML.setFocus()
        return
      }
      if (this.getMaximumCharactersTextField === 13) {
        const regex = new RegExp('[A-Z]{3}[0-9]{7}(/)[0-9]{2}')
        if (this.numberJournalEntriesXML.match(regex) === null) {
          this.numberJournalEntriesXML = ''
          this.$refs.NumberJournalEntriesXML.setFocus()
          return
        }
      } else {
        if (this.getMaximumCharactersTextField === 14) {
          const regex = new RegExp('[A-Z]{2}[0-9]{12}')
          if (this.numberJournalEntriesXML.match(regex) === null) {
            this.numberJournalEntriesXML = ''
            this.$refs.NumberJournalEntriesXML.setFocus()
            return
          }
        }
      }
      this.downloadingJournalEntriesXML = false
      // console.log(this.$refs.NumberJournalEntriesXML.value)
    },
  },
}
</script>
<template>
  <div class="flex flex-col w-full px-10 justify-center pb-15">
    <div class="flex flex-col w-full wrapper">
      <div class="flex title text-2xl">
        Balanza de Comprobación
      </div>
      <div class="flex -ml-4 juditfy-between mt-1">
        <div class="flex w-full">
          <div class="flex w-46">
            <GxPeriodsDropdown
              v-if="beginningPeriod && endingPeriod"
              ref="PeriodInit"
              :beginningPeriod="beginningPeriod"
              :endingPeriod="endingPeriod"
              class="mt-2"
              placeholder="Período inicial"
              label="Período inicial"
              :allow-empty="false"
              @selectedPeriod="selectedPeriodInit"
            />
          </div>
          <div class="w-46 -ml-4">
            <GxPeriodsDropdown
              v-if="beginningPeriod && endingPeriod"
              ref="PeriodEnd"
              :beginningPeriod="beginningPeriod"
              :endingPeriod="endingPeriod"
              class="mt-2"
              placeholder="Período final"
              label="Período final"
              :allow-empty="false"
              @selectedPeriod="selectedPeriodEnd"
            />
          </div>
          <div class="w-46 -ml-4">
            <GxDropdown
              v-if="getLevelsList"
              watch-value
              v-model="level"
              class="mt-2"
              placeholder="Niveles"
              label="Niveles"
              :options="getLevelsList"
            />
          </div>
        </div>
        <div class="flex">
          <GxButtonActions
            class="mt-5"
            v-if="this.beginningPeriod"
            :buttonsActions="getButtonsActions"
            @downloadPDF="downloadPDF"
            @downloadExcel="downloadExcel"
            @trialBalanceXML="trialBalanceXML"
            @journalEntriesXML="journalEntriesXML"
          />
        </div>
      </div>

      <div
        class="flex w-full mt-10"
        v-if="this.$apollo.queries.getTrialBalance.loading"
        ><GxLoader />
      </div>
      <div v-if="trialBalance" class="flex w-full -mt-3">
        <GxTrialBalance :trialBalance="this.trialBalance" />
      </div>
    </div>

    <GxModal v-if="showDownloadTrialBalanceXML">
      <div class="flex flex-col w-125 bg-white rounded-lg">
        <div class="flex flex-col w-full justify-center pt-9.5 pb-9.5">
          <span class="text-2xl font-bold text-center"
            >Exportar balanza de comprobación</span
          >
          <span class="text-base text-center mt-2"
            >Selecciona el tipo de envío de la balanza</span
          >
        </div>
        <div
          class="flex flex-col w-full -mt-10"
          :class="{ 'mb-9': this.typeTrialBalanceXML === '' }"
        >
          <div class="flex w-full justify-center">
            <div class="flex w-62">
              <GxDropdown
                watch-value
                v-model="typeTrialBalanceXML"
                class="mt-2"
                placeholder="Tipo de envío *"
                label="Tipo de envío *"
                :options="optionsTypeTrialBalanceXML"
              />
            </div>
          </div>
          <transition name="fade">
            <div
              v-if="this.typeTrialBalanceXML === 'Complementaria'"
              class="flex justify-center w-full"
            >
              <div class="flex -mt-3 ">
                <GxTextFieldDatePicker
                  required
                  :class="[' w-54']"
                  v-model="dateTrialBalanceXML"
                  label="Fecha de última modificación contable *"
                />
                <!-- :minDatetime="getBeginningPeriod"
            :maxDatetime="getEndingPeriod" -->
              </div>
            </div>
          </transition>
        </div>

        <div
          v-if="this.typeTrialBalanceXML && this.downloadingTrialBalanceXML"
          class="flex w-full pb-8"
          :class="{
            'mt-10': this.typeTrialBalanceXML === 'Normal',
            'mt-8': this.typeTrialBalanceXML === 'Complementaria',
          }"
        >
          <div class="flex inline-block justify-end w-1/2">
            <GxButtonTaxes
              :icon="'arrow_back'"
              class="bottom-0 rounded-md gx-button h-10 w-40 mr-2"
              :typeButton="'none'"
              v-bind:style="{ fontSize: '14px' }"
              @click="closeTrialBalanceXML"
            >
              <!-- @click="deleteJournalEntries" -->

              Cancelar
            </GxButtonTaxes></div
          >
          <div class="flex inline-block w-1/2 justify-start">
            <!-- @click="addRow" -->
            <GxButtonTaxes
              :icon="'launch'"
              class="bottom-0 rounded-md gx-button h-10 w-40 ml-2"
              v-bind:style="{ fontSize: '14px' }"
              :iconSize="'26px'"
              @click="downloadingTrialBalanceXML = false"
            >
              Exportar archivo
            </GxButtonTaxes>
          </div>
        </div>
        <div
          v-else-if="!this.downloadingTrialBalanceXML"
          class="flex w-full pb-8 mt-7"
        >
          <GxLoader />
        </div>
      </div>
    </GxModal>

    <GxModal v-if="showDownloadJournalEntriesXML">
      <div class="flex w-full h-80 items-center content-center">
        <div class="flex flex-col w-125 bg-white rounded-lg">
          <div class="flex flex-col w-full justify-center pt-9.5 pb-9.5">
            <span class="text-2xl font-bold text-center">Exportar pólizas</span>
            <span class="text-base text-center"
              >Selecciona el tipo de solicitud</span
            >
          </div>
          <div
            class="flex flex-col w-full -mt-10"
            :class="{ 'mb-9': this.typeJournalEntriesXML === '' }"
          >
            <div class="flex w-full justify-center">
              <div class="flex relative w-62">
                <GxDropdown
                  watch-value
                  v-model="typeJournalEntriesXML"
                  class="mt-2 "
                  placeholder="Tipo de solicitud *"
                  label="Tipo de solicitud *"
                  :options="optionsJournalEntriesXML"
                />
              </div>
            </div>
            <transition name="fade">
              <div
                v-if="this.typeJournalEntriesXML"
                class="flex justify-center w-full"
              >
                <div class="flex relative w-54 -mt-3">
                  <GxTextField
                    ref="NumberJournalEntriesXML"
                    :maxlength="getMaximumCharactersTextField"
                    v-model="numberJournalEntriesXML"
                    :label="getLabelTextField"
                    :placeholder="getLabelTextField"
                    autofocus
                  />
                </div>
              </div>
            </transition>
          </div>

          <div
            v-if="
              this.typeJournalEntriesXML && this.downloadingJournalEntriesXML
            "
            class="flex w-full pb-8 mt-7"
          >
            <div class="flex inline-block justify-end w-1/2">
              <GxButtonTaxes
                :icon="'arrow_back'"
                class="bottom-0 rounded-md gx-button h-10 w-40 mr-2"
                :typeButton="'none'"
                v-bind:style="{ fontSize: '14px' }"
                @click="closeJournalEntriesXML"
              >
                <!-- @click="deleteJournalEntries" -->

                Cancelar
              </GxButtonTaxes></div
            >
            <div class="flex inline-block w-1/2 justify-start">
              <!-- @click="addRow" -->
              <GxButtonTaxes
                :icon="'launch'"
                class="bottom-0 rounded-md gx-button h-10 w-40 ml-2"
                v-bind:style="{ fontSize: '14px' }"
                :iconSize="'26px'"
                @click="validateJournalEntriesXML"
              >
                Exportar archivo
              </GxButtonTaxes>
            </div>
          </div>
          <div
            v-else-if="!this.downloadingJournalEntriesXML"
            class="flex w-full pb-8 mt-7"
          >
            <GxLoader />
          </div>
        </div>
      </div>
    </GxModal>
  </div>
</template>
