<script>
import GxIcon from '@components/_Core/Icons/GxIcon'

export default {
  name: 'GxButton',
  components: {
    GxIcon,
  },
  props: {
    icon: {
      type: String,
      default: null,
    },
    dense: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    loadingSuccess: {
      type: Boolean,
      default: false,
    },
    variant: {
      type: Boolean,
      default: true,
    },
    variantSecondary: {
      type: Boolean,
      default: false,
    },
    heightVariant: {
      type: Boolean,
      default: true,
    },
    ghost: {
      type: Boolean,
      default: true,
    },
    full: {
      type: Boolean,
      default: false,
    },
    plain: {
      type: Boolean,
      default: false,
    },
    dark: {
      type: Boolean,
      default: false,
    },
    to: {
      type: Object,
      default: null,
    },
    type: {
      type: String,
      default: 'button',
    },
    typeButton: {
      type: String,
      default: 'general', // general, success, pending, none
    },
    iconSize: {
      type: String,
      default: '24px',
    },
  },
  data() {
    return {
      initialWidth: this.full ? '100%' : 'initial',
    }
  },
  computed: {
    getIconColor() {
      //general, success, pending, none
      switch (this.typeButton) {
        case 'general':
          return 'text-primary-500'
        case 'success':
          return 'text-success'
        case 'pending':
          return 'text-pending'
        case 'none':
          return 'text-gray-700'
        default:
          return 'text-primary-500'
      }
    },
    getColors() {
      //general, success, pending, none
      switch (this.typeButton) {
        case 'general':
          return 'border-primary-500 text-primary-500'
        case 'success':
          return 'border-success text-gray-700'
        case 'pending':
          return 'border-pending text-gray-700'
        case 'none':
          return 'border-gray-700 text-gray-700'
        default:
          return 'border-primary-500 text-primary-500'
      }
    },
    getColorsHover() {
      switch (this.typeButton) {
        case 'general':
          return 'focus:bg-secondary-50'
        case 'success':
          return 'focus:bg-success-hover'
        case 'pending':
          return 'focus:bg-pending-hover'
        case 'none':
          return 'focus:bg-gray-200'
        default:
          return 'focus:bg-secondary-50'
      }
    },
  },
  mounted() {
    if (!this.to) {
      this.$refs.button.style.setProperty('--initial-width', this.initialWidth)
    }

    // TODO: Fix ripple effect for unit test
    // const clickPosition = (evt) => {
    // const clientRect = evt.currentTarget.getBoundingClientRect()
    // const x = evt.pageX - clientRect.left
    // const y = evt.pageY - clientRect.top
    // this.$refs.button.style.setProperty('--ripple-x', `${x}px`)
    // this.$refs.button.style.setProperty('--ripple-y', `${y}px`)
    // }
    // this.$el.addEventListener('click', clickPosition)
    // this.$once('hook:beforeDestroy', () => {
    //   this.$el.removeEventListener('click', clickPosition)
    // })
    // if (this.plain) return
    // if (this.dark) {
    //   this.setRippleBackground('#D8C6FE')
    //   return
    // }
    // if (this.ghost) {
    //   this.setRippleBackground('#F1E8FF')
    //   return
    // }
    // this.setRippleBackground('#FFFFFF')
  },
  methods: {
    setRippleBackground(background) {
      this.$refs.button.style.setProperty('--ripple-background', background)
    },
  },
}
</script>

<template>
  <component
    :is="to ? 'router-link' : 'button'"
    ref="button"
    :to="to"
    class="flex items-center outline-none focus:outline-none"
    :class="[
      getColors,
      getColorsHover,
      {
        ripple: !plain,
        loading: loading,
        loadingSuccess: loadingSuccess,
        'gx-height-variant': heightVariant,
        'gx-button--plain': plain,
        'px-2 mb-2': !plain && !variantSecondary && !variant,
        'w-full': full,
        'bg-primary-500': loading,
        'rounded-full':
          (!variant && !plain && !variantSecondary) || (plain && loading),
        'rounded-lg variant-button': variant,
        'gx-button': !variant,
        rounded: variantSecondary,

        'bg-transparent': plain,
        'hover:text-secondary-300': plain && !disabled,
        'border  ': ghost,
        'border-gray-500 cursor-not-allowed': disabled || loading,
        'bg-gray-300': disabled && !loading && !ghost && !plain,
        'bg-white': (disabled || loading) && ghost && !variant,
        'hover:text-primary-400 hover:border-primary-400 focus:border-secondary-300':
          plain && !disabled,
        '': ghost && variant && !dark,
      },
    ]"
    :type="to ? 'text/html' : type"
    :disabled="disabled || loading || loadingSuccess"
    :data-testid="$slots.default[0].text"
    v-on="$listeners"
  >
    <GxIcon
      v-if="loadingSuccess"
      class="material-icons absolute"
      :class="{ 'text-white': !plain && !dark, 'text-primary-500': plain }"
      aria-hidden="true"
      focusable="false"
      icon="done"
    />
    <GxIcon
      v-if="icon"
      :class="[
        getIconColor,
        { invisible: loading || loadingSuccess, 'mr-4': $mq === 'mo' },
      ]"
      v-bind:style="{ fontSize: iconSize }"
      class="material-icons -ml-px mr-1.5"
      aria-hidden="true"
      focusable="false"
      :icon="icon"
    />
    <div
      :class="{
        invisible: loading || loadingSuccess,
        'pointer-events-none': disabled,
      }"
      class="slotContainer"
    >
      <slot />
    </div>
  </component>
</template>

<style lang="scss" scoped>
/* stylelint-disable */
.gx-button {
  --initial-width: initial;

  min-width: 2rem;
  min-height: 3.125rem;
  font-family: 'Nunito';
  font-size: 1.125rem;
  line-height: 1.75rem;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  // Make loading end transition .1 seconds slower since it has just an icon and it looks like it's snapping faster on exit.
  transition: all 0.6s ease-in-out;
}
.gx-button--plain {
  min-width: initial;
  min-height: 24px;
}
.gx-button--plain .slotContainer {
  top: -2px;
}
.gx-button i {
  font-size: inherit;
}

@screen de {
  .variant-button {
    min-height: 1.75rem;
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    font-family: 'Nunito';
    font-size: 0.75rem;
  }
  .gx-button {
    min-height: 2.75rem;
    font-size: 0.75rem;
  }
  .slotContainer {
    top: 0;
    letter-spacing: 0;
  }
  .gx-button--plain .slotContainer {
    top: 0;
  }
  .gx-height-variant {
    min-height: 1.5rem;
  }
  .gx-height-variant .slotContainer {
    top: -1px;
  }
  .gx-height-variant-secondary {
    min-width: 0;
    min-height: 1.125rem;
    font-size: 0.625rem;
    line-height: 15px;
    text-transform: none;
  }
}

// Ripple

/* Ripple effect */
.ripple {
  --ripple-x: 0;
  --ripple-y: 0;
  --ripple-background: white;

  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
}

.ripple::after {
  position: absolute;
  top: calc(var(--ripple-y) - 32.5px);
  left: calc(var(--ripple-x) - 31px);
  display: none;
  width: 61px;
  height: 65px;
  pointer-events: none;
  content: '';
  background-color: var(--ripple-background);
  border-radius: 50%;
  opacity: 0;
  transform-origin: center center;
  animation: ripple 1s;
}

.ripple:focus:not(:active)::after {
  display: block;
}

@keyframes ripple {
  from {
    opacity: 0.5;
    transform: scale(0);
  }
  to {
    opacity: 0;
    transform: scale(10);
  }
}
// Loader
@keyframes spin {
  from {
    background: transparent;
    border: 2px solid var(--secondary-300);
    border-top-color: var(--primary-500);
    transform: rotate(0deg);
  }
  to {
    background: transparent;
    border: 2px solid var(--secondary-300);
    border-top-color: var(--primary-500);
    transform: rotate(360deg);
  }
}

.loading {
  width: 50px;
  min-width: 50px;
  height: 50px;
  transition: all 3s ease-in-out;
  animation-name: spin;
  animation-duration: 0.6s;
  animation-timing-function: linear;
  animation-delay: 1.3s;
  animation-iteration-count: infinite;
}

@screen de {
  .loading {
    width: 44px;
    min-width: 44px;
    height: 44px;
  }
}
</style>
